<template>
		<div class="wrap" @click="fn">
			<!--<img src="../assets/img/1.png"><br />-->
			<span v-show="normalShow"><slot name="normalImg" ></slot><br /></span>
			<span v-show="!normalShow"><slot name="activeImg"></slot><br /></span>
			<span :class="{red:!normalShow}">{{txt}}</span>
		</div>
</template>
<script type="text/javascript">
	export default {
		props:["txt","mark","sel"],
		data:function(){
			return{
//				normalShow:true
			}
		},
		methods:{
			fn:function(){
				this.$emit("change",this.mark);//
				//console.log("mark: "+this.mark);//thismark
				//console.log("sel: "+this.sel)//thismark
			}
		},
		components:{
		   			 
		},
		computed:{//
			normalShow:function(){//bor
				if(this.mark==this.sel){
					return false;//
				};
				return true;
			}
		}
	}
</script>
<style>
	.wrap{width:20%;text-align: center;float:left}
	.wrap img{width:0.45rem;}
	.wrap span{width:100%;font-size: 0.12rem;float:left;}
	.wrap .red{color:#EF7489}
</style>


