<template>
		<div class="nav_wrap">
			<Itemtaber txt="" mark="home" :sel="selector" @change="getval">
				<img src="../assets/img/1.png" slot="normalImg">
				<img src="../assets/img/6.png" slot="activeImg">
			</Itemtaber>
			<Itemtaber txt="" mark="audios" :sel="selector" @change="getval">
				<img src="../assets/img/2.png" slot="normalImg">
				<img src="../assets/img/7.png" slot="activeImg">
			</Itemtaber>
			<Itemtaber txt="" mark="room" :sel="selector" @change="getval">
				<img src="../assets/img/3.png" slot="normalImg">
				<img src="../assets/img/8.png" slot="activeImg">
			</Itemtaber>
			<Itemtaber txt="" mark="find" :sel="selector" @change="getval">
				<img src="../assets/img/4.png" slot="normalImg">
				<img src="../assets/img/9.png" slot="activeImg">
			</Itemtaber>
			<Itemtaber txt="" mark="user" :sel="selector" @change="getval">
				<img src="../assets/img/5.png" slot="normalImg">
				<img src="../assets/img/10.png" slot="activeImg">
			</Itemtaber>
		</div>
</template>
<script>
	import Itemtaber from './Itemtaber.vue';
	export default {
		data:function(){
			return{
				selector:"home",
			}
		},
		methods:{
			getval:function(val){
				this.selector=val;
				this.$router.push("/"+this.selector);//
//				console.log("selector:"+this.selector)
			}
		},
		components:{
		   	Itemtaber
		}
	}

</script>
<style>
	.nav_wrap{float:left;position: fixed;height:0.6rem;border-top:0.01rem solid #B6B6B6;bottom: 0rem;width:100%;background:#fff;z-index: 99;}
	
	/**/
</style>
