<template>
	<ul class="headerWrap">
		<!---->
		<!--<router-link to="/audios/movie"><Mynavli txts="" mark="movie" @change="getval" :sel="selector"></Mynavli></router-link>-->
		<Mynavli txts="" mark="movie" @change="getval" :sel="selector"></Mynavli>
		<Mynavli txts="" mark="read" @change="getval" :sel="selector"></Mynavli>
		<Mynavli txts="" mark="tv" @change="getval" :sel="selector"></Mynavli>
		<Mynavli txts="" mark="city" @change="getval" :sel="selector"></Mynavli>
		<Mynavli txts="" mark="music" @change="getval" :sel="selector"></Mynavli>
	</ul>
</template>
<script type="text/javascript">
	import Mynavli from "./secondnavli.vue"
	export default {
		props:[],
		data:function(){
			return{
				selector:"movie"
			}
		},
		methods:{
			getval:function(val){
				this.selector=val;
				this.$router.push("/audios/"+this.selector);//
			}
		},
		components:{
		   	Mynavli 
		}
	}
</script>
<style>
	.headerWrap{height:0.39rem;border:0.01rem solid #e4e4e4}
	/**/
</style>


